.container {
  max-width: 480px;
  margin: 20px auto;
  padding: 0 15px;
  font-family: "Noto Sans TC", sans-serif;
}

.form {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 12px;
  justify-content: center;
}

.input {
  flex: 1 1 140px; /* 可伸縮，基準寬度140px */
  min-width: 120px;
  padding: 8px 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  transition: border-color 0.3s;
}

.input:focus {
  border-color: #4a90e2;
  outline: none;
}

.button {
  padding: 8px 16px;
  font-size: 1rem;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  flex: 0 0 auto;
}

.button:hover {
  background-color: #357abd;
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.list li {
  margin-bottom: 12px;
  padding: 8px 12px;
  background: #f9f9f9;
  border-radius: 6px;
  font-size: 1rem;
}

/* 響應式調整：手機更小字 */
@media (max-width: 480px) {
  .input {
    font-size: 0.9rem;
    padding: 6px 8px;
  }
  .button {
    font-size: 0.9rem;
    padding: 6px 12px;
    width: 100%;
  }
  .form {
    gap: 6px;
  }
  .list li {
    font-size: 0.9rem;
  }
}
